import React from 'react'
import './Signin.css'




const Signin = ({onRouteChange}) => {
	return(
	 <div className='center'>
          <div className='form'>
			  <div  className='containerSignin'>
			    <label className='label' htmlFor="uname"><b>Email</b></label>
			    <input className='label inputsignin' type="text" placeholder="" name="email" required/>

			    <label className='label' htmlFor="psw"><b>Password</b></label>
			    <input className='label inputsignin' type="password" placeholder="" name="psw" required/>

			    <button onClick={() => onRouteChange('home')} className='label button' type="submit" >Sign in</button>
			    <div className= 'register'>
			    <p onClick={() => onRouteChange('register')}>Register</p>
			    </div>
			    <label className='label remember'>
			      <input  type="checkbox" checked="checked" name="remember"/> Remember me
			    </label>
			  </div>

			  <div className= 'forgot'>
			    <span >Forgot <a href="h">password?</a></span>
			  </div>
			</div> 
      </div>
		)
}

export default Signin;