import React, {Component} from 'react'
import Particles from 'react-particles-js';
import './App.css';
import Navigation from './component/Navigation/Navigation'
import Logo from './component/Logo/Logo'
import ImageLinkForm from './component/ImageLinkForm/ImageLinkForm'
import Rank from './component/Rank/Rank'
import FaceRecognition from './component/FaceRecognition/FaceRecognition'
import Signin from './component/Signin/Signin'
import Register from './component/Register/Register'

import Clarifai from "clarifai";
import {Helmet} from "react-helmet";



const app= new Clarifai.App({
 apiKey: '1fc3658312ab4b609f89b7d6168dd273'
});

const particlesOptions = {
    particles: {
                  number: {
                    value: 150,
                    density: {
                      enable: true,
                      value_area: 1000
                    }
                  },        
} }



class App extends Component {
   constructor() {
    super();
    this.state = {
      input:'',
      imageURL:'',
      stars:'',
      route:'signin'
    }
   }

        detectFace = (data) => {
           
           return data.outputs[0].data.regions[0].data.concepts[0].name;

        
        }
       displayFaceStars= (stars) =>{
          this.setState({stars: stars});

       }

      
        onInputChange = (event) => {
           this.setState({input: event.target.value});
        }

        onSubmit = () => {
          this.setState({imageURL: this.state.input});
          app.models
           .predict('e466caa0619f444ab97497640cefc4dc', this.state.input )
           .then(response => this.displayFaceStars(this.detectFace(response)))
           .catch(err => console.log(err));
            
            
        }
        onRouteChange = (route) =>{
          this.setState({route: route})
        }

          render() {
           const {imageURL, stars, route} = this.state;
           
            return (
             
              
          
              <div className="App">
              <Helmet>
              <title>My Title</title>
               <meta name="viewport" content="width=device-width, initial-scale=1"/>  
              </Helmet>
                <Particles  className='particles'
                        params={particlesOptions}
                          
                       
                      />
                
                {route === 'home' 
                 ? <div>
                       <Navigation onRouteChange={this.onRouteChange}/>
                       
                       <Rank />
                       < ImageLinkForm 
                      onInputChange={this.onInputChange}
                      onButtonSubmit={this.onSubmit}
                      />
                       <FaceRecognition 
                      imgUrl={imageURL}
                      stars={stars}
                      /> 
                    </div>
                  : ( 
                    route === 'signin'
                       ? <div> 
                            <Logo />
                            <Signin onRouteChange={this.onRouteChange}/>

                         </div>
                       : <div> 
                            <Logo />
                            <Register onRouteChange={this.onRouteChange} />
                        
                         </div>
                )
                }
              </div>
            );
        }
        }
          
export default App;

