import React from 'react'




const FaceRecognition = ({imgUrl, stars}) => {
	return(
		<div>
         <p className='startText' >{stars}</p>
		<div className ='center imgDiv'>
		  <img id='inputimage' src= {imgUrl} alt='' style={{width:300, height:'auto', padding: 20,}} />
		  
		</div>
      </div>
		)
}

export default FaceRecognition;
