import React from 'react'





const Register = ({onRouteChange}) => {
	return(
	 <div className='center'>
          <div className='form'>
			  <div  className='containerSignin'>
                <label className='label' htmlFor="uname"><b>Name</b></label>
			    <input className='label inputsignin' type="text" placeholder="" name="name" required/>

			    <label className='label' htmlFor="email"><b>Email</b></label>
			    <input className='label inputsignin' type="text" placeholder="" name="email" required/>

			    <label className='label' htmlFor="psw"><b>Password</b></label>
			    <input className='label inputsignin' type="password" placeholder="" name="psw" required/>

			    <button onClick={() => onRouteChange('home')} className='label button' type="submit" >Register</button>
			    <div className= 'register'>
			    <p onClick={() => onRouteChange('signin')}>Signin</p>
			    </div>
			    <label className='label remember'>
			      <input  type="checkbox" checked="checked" name="remember"/> Remember me
			    </label>
			  </div>
			</div> 
      </div>
		)
}

export default Register;